@import url(https://fonts.googleapis.com/css?family=Varela+Round);
@import url(https://fonts.googleapis.com/css?family=Orbitron);
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  padding: 1rem;
  background: #000;
  font-family: 'Varela Round', sans-serif;
}

.hide {
  display: none;
}

#root {
  height: 100%;
}

.height-100 {
  height: 100%;
}

.App {
  border: 3px solid #fff;
  border-radius: 1px;
}

.grid {
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}
.row {
  width: auto;
  flex-direction: row;
  display: flex;
  border-bottom: 6px solid #96B7E7;
}
.cell {
  border-right: 6px solid #96B7E7;
  padding: 5px;
  width: 5rem;
  height: 5rem;
  line-height: 20px;
  font-weight: bold;
  font-size: 2em;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;

  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  -webkit-transition: all 1s ease-in;
  transition: all 1s ease-in;
}
.cell--set {
  color: #96B7E7;
}

.cell:first-child {
  border-top: 0;
  border-left: 0;
}

.cell:last-child {
  border-right: 0;
  border-bottom: 0;
}

.row:first-child {
  border-top: 0;
}

.row:last-child {
  border-bottom: 0;
}

.text-white {
  color: white;
}

.WinGame {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 4rem;
}

.TieGame {
  font-family: 'Orbitron', sans-serif;
  color: #96B7E7;
  text-transform: uppercase;
  padding: 2rem;
}

.TieGame p {
  margin: 1.5rem;
  padding: 0;
  line-height: 2rem;
  font-size: 2rem;
  text-shadow: 0 0 2px #73decf;
}

.TieGame p:first-child {
  margin-top: 0;
}

.TieGame p:last-child {
  margin-bottom: 0;
}

